/**
 * ギャラリー (WordPress標準のもの)
 *   投稿タイプ"ギャラリー”は関係ない
 *   TODO: カラムレイアウト、レスポンシブ対応する
 */
.gallery { // div、 .galleryid-3783.gallery-columns-5.gallery-size-medium
	margin-top: 2rem;
	margin-bottom: 2rem;
	@media($_sm){
		font-size: $font-small;
	}
}
.gallery-columns-1 {} // 各ギャラリーに連番が付く
.gallery-columns-2 {}
.gallery-columns-3 {}

	.gallery-item { //figure、各画像(共通)、need for visual editor
		width: 100%;
	}
	    .gallery-icon {//div、画像を囲む要素
	    	position: relative;
	        text-align: center;
	        &.landscape {} // 横位置写真、もしくは正方形
	        &.portrait {} // 縦位置写真
	        a {
	          img {
	          	width: 100%;
	          }
	          &:after {
				@include my-fa-icon-base;
				font-family: "Font Awesome 5 Free";
				font-weight: 900; // regular のアイコンフォントなら 400
				content: "\f065";

	          	position: absolute;
	          	right: 8px;
	          	bottom: 5px;
				color: white;
				opacity: .7;
	          }
	        }
	    }
	    .gallery-caption {//figcaption、.wp-caption-text併記、※Theme Check 必須
	        text-align: center;
	        line-height: 1.1;
	    }



@media($sm_) {
	.gallery-columns-2 {
		display: flex;
		flex-wrap: wrap;
		.gallery-item {
			flex-basis: 47%;
			margin: 1%;
		}
	}
}

.gallery-columns-3,
.gallery-columns-4,
.gallery-columns-5,
.gallery-columns-6 {
	display: flex;
	flex-wrap: wrap;
	.gallery-item {
		flex-basis: 47%;
		margin: 1%;
	}
}
@media($_sm){
	.gallery-columns-5,
	.gallery-columns-6 {
		.gallery-item {
			flex-basis: 30%;
		}
	}
}
@media($sm_md){
	.gallery-columns-4 {
		.gallery-item {
			flex-basis: 30%;
		}
	}
	.gallery-columns-5 {
		.gallery-item {
			flex-basis: 23%;
		}
	}
	.gallery-columns-6 {
		.gallery-item {
			flex-basis: 18%;
		}
	}
}
@media($md_){
	.gallery-columns-3 {
		.gallery-item {
			flex-basis: 30%;
		}
	}
	.gallery-columns-4 {
		.gallery-item {
			flex-basis: 23%;
		}
	}
	.gallery-columns-5 {
		.gallery-item {
			flex-basis: 18%;
		}
	}
	.gallery-columns-6 {
		.gallery-item {
			flex-basis: 14%;
		}
	}
}

